.mainModalEditUserWrap {
    width: 335px;
    height: 440px;
    padding: 24px;
    background-color: var(--bgModal);
    margin-left: auto;
    margin-right: auto;
    border-radius: 8px;
    box-shadow: 0px 4px 16px 0px rgba(22, 22, 22, 0.05);
    margin-top: 40px;
    margin-bottom: 40px;
    
    @include minMd() {
        width: 400px;
    }
}

