.deleteBoardConfirm {
   display: flex;
   flex-direction: column;
   position: absolute;
   visibility: hidden;
   // right: -100%;
   // overflow-x: visible;
   opacity: 0;
         width: 150px;
         height: 61px;
   border-radius: 4px;
   background: #BEDBB0;
   padding: 5px 10px;
            transform: translate(-45%);
   // z-index: 9999999;
   &:hover {
         transform: translate(38%);
            transition: all .3s ease-in-out;
               opacity: 1;
}   
}


.confirmDeleteButton {   
      padding: 0;
               font-size: 12px;
         border: none;
         background-color: #BEDBB0;
         margin-bottom: 5px;
         &:hover {
                     transform: scale(1.1);
                                    background-color: #9DC888;
         }
}

.confirmDeleteTitle {
   color: #161616;      
   text-align: center;   
   font-family: map-get($fonts, r), sans-serif;
         font-size: 12px;
         letter-spacing: -0.28px;
}
